//  -    JS - Swiper-Product.js

import React, { useRef, useState } from "react";
import $ from 'jquery';
import { easeOutExpo } from "jquery-ui";
import { Swiper, SwiperSlide } from "swiper/react";
import {Link} from 'react-router-dom';

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "./swiperProduct.css";
import Arrow from "../common/Arrow";




//////////////////////////// [     ] ////////////////////////////
const slideUpAni = ()=>{
    $(()=>{
        
    const pd_slide = $(".slide.gin");
    const pd_tit = $(".product_tit.gin");
    // console.log("  : ", pd_slide);

    // [ ()    ]
    //   /      
    pd_slide.hover(
        // 
        function(){
            //     
            let hval = $(this).find(".product_tit.gin").height();
            // console.log("     : ", hval);

            //        
            $(this).siblings().find("img").stop().animate({
                opacity: "0.5"
            }, 400);

            //       
            $(this).find(".wrap").stop().animate({
                height: hval + "px",
                opacity: "1"
            }, 500, easeOutExpo);
        },
        // 
        function(){
            // console.log(" ");

            //        
            $(this).siblings().find("img").stop().animate({
                opacity: "1"
            }, 400);

            //    0   
            $(this).find(".wrap").stop().animate({
                height: "0px",
                opacity: "0"
            }, 500, easeOutExpo);
        }
    );

    
});

} //////////////////////// slideUpAni  ///////////////////////////////


//     




// [   ]
export default function SwiperProduct(props) {
    // props.pgname -   ( )

    
    
    return (
        <>
            {/* 1.   */}
            <div className="swipe_tit_container">
                <div className="wrap">
                    <h2 className="title">
                        <p>Our gins made in</p>
                        <p>France</p>
                    </h2>
                </div>
            </div>
            {/* 2.   */}
            <div className="swipe_deco_container">
                <div className="wrap">
                    <img className="deco_img" src="./images/dt/sub/genevrier.png" alt="genevrier " />
                </div>
            </div>
            {/* 3.      */}
            <Arrow />
            {/* 4.  */}
            <section className="swipe_banner">
                <Swiper
                    slidesPerView={2}
                    // "Limited"    30  0
                    spaceBetween={props.pgname=="Limited"?30:0}
                    centeredSlides={true}
                    breakpoints={{
                        200: {
                            slidesPerView: 2,
                        },
                        700: {
                            slidesPerView: 3,
                        },
                        1200: {
                            slidesPerView: 4,
                        }
                    }}
                    className="swiper-product"
                >
                    <ul className="swiper-wrapper">
                        <SwiperSlide className="swiper-slide slide gin">
                            <Link to='/original'>
                                <img src="./images/dt/sub/original.png" alt=" " />
                                <div className="wrap">
                                    <span className="product_tit gin">Original</span>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide className="swiper-slide slide gin">
                            <Link to="/dete">
                                <img src="./images/dt/sub/dete.png" alt=" " />
                                <div className="wrap">
                                    <span className="product_tit gin">Jardin <br /> dt</span>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide className="swiper-slide slide gin">
                            <Link to='/rouge'>
                                <img src="./images/dt/sub/rouge.png" alt=" " />
                                <div className="wrap">
                                    <span className="product_tit gin">Rouge</span>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide className="swiper-slide slide gin">
                            <Link to='/limited'>
                                <img src="./images/dt/sub/limited.png" alt=" " />
                                <div className="wrap">
                                    <span className="product_tit gin">Our limited <br /> editions</span>
                                </div>
                            </Link>
                        </SwiperSlide>
                    </ul>
                </Swiper>
            </section>
            {/* js    */}
            {slideUpAni()}
        </>
    );
}



