// limited        JS - Details.js

import React, { useEffect } from "react";
import $ from 'jquery';
import '../../css/details.css';
import limited_product_data from "../../data/limitedProduct";
//  
import Article from "./LimitedArticle";

// [  ]
const Details = (props)=>{ // props.dbseq -   
    //  
    const selecData = limited_product_data;

    
    
    //    details  
    const clsFn = ()=>{
        console.log("!");
        //  
        const details_container = $(".details_container");

        setTimeout(() => {
            //   
            details_container.css({
                clipPath: `circle(0% at 50% 50%)`,
            });
            
            //        
            $("body").css({overflowY:"visible"});
        }, 10, 
        ()=>{
            //     
            details_container.scrollTop(0);
        });

    };




    return(
        <div className="details_container">
        <div id="details">
            <section className="details">
                {/* 1.    */}
                <div className="cls_btn_container">
                    <div>
                        <span className="material-symbols-outlined close" onClick={clsFn}>close</span>
                    </div>
                </div>
                {/* 2.  */}
                <div className="product_name_container">
                    <div className="wrap">
                        <h2 className="name">
                            <span data-aos="fade-up" data-aos-duration="1000" data-aos-delay="700" data-aos-easing="ease-in-out-quart">
                                {
                                    selecData[props.dbseq]["productName"][1]
                                }
                            </span>
                        </h2>
                    </div>
                </div>
                {/*    */}
                <aside className="gridBx">
                    {/* 3.  */}
                    <div className="product_img_container">
                        <div className="wrap">
                            <img id="product" src={selecData[props.dbseq]["bigSrc"]} alt="  " />
                        </div>
                    </div>
                    {/* 4.   */}
                    <div className="product_desc_container">
                        <Article pgname="details" dbseq={props.dbseq} />
                    </div>
                </aside>            
            </section>
        </div>
        </div>
    );
}; //////////////////////// Details  ////////////////////////

// [  ]
export default Details;