//       - LegalAgeVerification.js

import React, { useEffect } from 'react';
import $ from 'jquery';
import Modal from 'react-modal';
import { useState } from 'react';
import { useCookies } from 'react-cookie';
// CSS
import '../../css/legalAgeVerification.css';
import Loading from './Loading';


// npm install react-cookie ->    
// npm install react-modal    !!


const LegalAgeVerification = ()=>{

    //    !
    const [isHover, setIsHover] = useState(false);

    // useState    ! :  true =   
    const [modalIsOpen, setModalIsOpen] = useState(true);

    // hasCookie state   
    //     state  : true   
    const [hasCookie, setHasCookie] = useState(true);

    //  
    const [cookies, setCookies] = useCookies();


    //      : pm           
    const getExpiredDate = (days)=>{
        // Date   :  Date   ?       ...!
        const date = new Date();
        //  (?)
        date.setDate(date.getDate() + days);
        //  
        return date;
    }; ////////////////// getExpiredDate  //////////////////


    //  1     
    const closeModalUntilExpires = ()=>{
        //  false(?)     (?)
        if(!cookies) return;
        
        console.log("   !");

        //  1 
        const expires = getExpiredDate(1);
        //  1  
        setCookies("MODAL_EXPIRES", true, {path: "/", expires: expires});

        //  Hook  
        setModalIsOpen(false);
    }; ////////////////// closeModalUntilExpires  //////////////////

    // useEffect      ,      
    useEffect(()=>{
        //   "MODAL_EXPIRES"   
        if(cookies["MODAL_EXPIRES"]) return;

        // "MODAL_EXPIRES"   (?) 
        console.log(cookies["MODAL_EXPIRES"]);

        //    Hook  false (?)
        setHasCookie(false);
    }, []);
    //  Modal  closeModalUntilExpires  prop  "    "  onClick  

    


    //      
    const hoverFn = ()=>{
        $(()=>{

            $(".yes_bx").mouseenter(function(){
                // console.log("  !", $(this).siblings());

                $(this).siblings().find("h3").stop().animate({
                    opacity:"0.5",
                    
                }, 300);
            });
            $(".yes_bx").mouseleave(function(){
                $(this).siblings().find("h3").stop().animate({
                    opacity:"1",
                    
                }, 300);
            });

            $(".no_bx").mouseenter(function(){
                // console.log("  !", $(this).siblings());

                $(this).siblings().find("h3").stop().animate({
                    opacity:"0.5",
                    
                }, 300);
            });
            $(".no_bx").mouseleave(function(){
                $(this).siblings().find("h3").stop().animate({
                    opacity:"1",
                    
                }, 300);
            });
            
        });
    };

    //  
    const openModal = () => {
        setModalIsOpen(true);
    };
    //  
    const closeModal = ()=> {
        setModalIsOpen(false);
    };

    // no     
    const no_pick = ()=>{
        console.log("!  !");

        //  
        $(".no_pick_bx").css({display: "block"});
    };




    return(
        <>
            <section className='legal_age_container'>
                {/* isOpen () true   ! */}
                {
                    modalIsOpen && !hasCookie && (

                    <Modal
                        isOpen={modalIsOpen}
                        closeModal={()=> setModalIsOpen(false)}
                        closeModalUntilExpires={closeModalUntilExpires}
                        className="modal" 
                        overlayClassName="overlay" 
                        closeTimeoutMS={500} 
                        >
                        {/* 
                            1.         esc       .    onRequsetClose={callback}! onRequestClose  isOpen   false     
                            2.       CSS   ,           'closeTimeoutMS'     !
                        */}
                        
                        <div>
                            {/* 1.  */}
                            <div className="logo_bx">
                                <img src="./images/logo.png" alt=" " />
                            </div>
                            {/* 2.  */}
                            <div className='title_bx'>
                                <h2 className='title'>
                                    <p className='txt' >
                                        <span data-aos="fade-up" data-aos-duration="800" data-aos-delay="300">Citadelle Gin,</span>
                                    </p>
                                    <p className='txt' >
                                        <span data-aos="fade-up" data-aos-duration="800" data-aos-delay="400">THE french gin.</span>
                                    </p>
                                </h2>
                            </div>
                            {/* 3.   */}
                            <div className='verification_bx'>
                                <div className='query_desc'>
                                    <p data-aos="fade" data-aos-duration="800" data-aos-delay="800">
                                        Are you of legal age to access our website?
                                    </p>
                                </div>
                            </div>
                            {/* 4. y/n   */}
                            <div className='YoN_bx' data-aos="fade" data-aos-duration="800" data-aos-delay="1000">
                                <div className='yes_bx' onClick={closeModal}>
                                    <h3>Yes</h3>
                                </div>
                                <div className='no_bx' onClick={no_pick}>
                                    <h3>No</h3>
                                </div>
                            </div>
                            {/* 4-1. no     */}
                            {

                            }
                            <div className='no_pick_bx'>
                                <div className='msg'>
                                    <p>
                                         You are not old enough to view this content
                                    </p>
                                </div>
                            </div>
                            {/* 5.    */}
                            <div className='cookieChk_bx'>
                                {/*  */}
                                <div className='chk_bx' >
                                    <input type='checkbox' id='checker' className='noShowChk' onClick={closeModalUntilExpires} ></input>
                                </div>
                                {/*  */}
                                <div className='desc_bx'>
                                    <label htmlFor="checker" >
                                        Remember me for 7 days (Unless Shared)
                                    </label>
                                </div>
                            </div>
                        </div>

                    </Modal>
                    )
                }
            </section>
            {/*      */}
            {hoverFn()}
        </>
    );
}; /////////////// LegalAgeVerification  ///////////////

// 
export default LegalAgeVerification;

//  warning.js:34 Warning: react-modal: App element is not defined. Please use `Modal.setAppElement(el)` or set `appElement={el}`. This is needed so screen readers don't see main content when modal is opened. It is not recommended, but you can opt-out by setting `ariaHideApp={false}`.   
//              content        .     #root     App.js    .

//         
Modal.setAppElement("#root");



