/*
  Promise 
  1.Pending()     
  2,Fulfilled()      Promise   
  3.Rejected()       

  [Pending]
  new Promise();
  new Promise((resolve, reject)=>{});
  ->Promise    
  - resolve(), reject() 

  [Fulfilled]
  // [Fulfilled]
  function getData(){
    // resolve()  Promise 
    return new Promise ((resolve, reject)=>{
      let data = 10;
      resolve(data);
    })
  }
  getData().then((resolveedData)=> console.log(resolveedData));
  // Promise   resolve   Promise  .  then()      .

*/
















