//      JS - ScrollTop.js

import { useEffect } from "react";
import { useLocation } from "react-router-dom";
// AOS 
import AOS from 'aos';
import 'aos/dist/aos.css';


//  
const ScrollTop = ()=>{
    //    
    const {pathname} = useLocation();

    //   
    useEffect(()=>{
        //   
        window.scrollTo(0, 0);

        // AOS  : DOM             setTimeout 
        setTimeout(()=>AOS.init(), 50);
        
    }, [pathname]); /////////////// useEffect() ///////////////

    // null :      
    return null;

}; /////////////////////// ScrollTop  ///////////////////////

// 
export default ScrollTop;