//      JS - SectionIntro.js

import React, { useEffect } from 'react';
import $ from 'jquery';
import '../../css/sectionIntro.css';
import sectionIntro_data from '../../data/sectionIntro';
import RollingBanner from './RollingBanner';


// [      ]
const SectionIntro = (props)=>{
    //props.snum -  
    // props.cls -   css    

    //  
    const slcData = sectionIntro_data;

    return(
        <>
            {/*    */}
            <div id="section_intro" className={props.cls}>
                <section className="section_intro">
                    <article>
                        <div>
                            {
                                props.snum == 0 ?
                                <img className='bgimg-right' src='./images/dt/main/background-right.png' alt='' /> :
                                <img className='bgimg-left' src='./images/dt/main/background-left.png' alt='' />
                            }
                        </div>
                        {/* 1.   */}
                        <div className='sec_title_container'>
                            <div className='title_bx'>
                                <h2 className={'title' + " " + props.cls}>
                                    {/*        ->          title        */}
                                {
                                    slcData[props.snum]["title"].map((v, i)=>
                                        <span key={i}>
                                            <span className='letter' data-aos="fade-up" data-aos-duration="1200" data-aos-delay="100">{v}</span>
                                        </span>
                                    )
                                }
                                </h2>
                            </div>
                        </div>
                        {/* 2.   */}
                        <div className='sec_desc_container'>
                            <div className='desc_bx'>
                                <p className={'desc' + " " + props.cls}>{slcData[props.snum]["desc"]}</p>
                            </div>
                        </div>
                        {/* 3-1.snum 1     */}
                        {
                            props.snum == 1 &&
                            <RollingBanner />
                        }
                        {/* 3-2.   */}
                        <div className='sec_img_container'>
                            <div className={'img_bx' + " " + props.cls}>
                                {/* 2        */}
                                {
                                    props.cls == "sec2" ?
                                    <img className={props.cls} src={slcData[props.snum]["isrc"]} alt='' /> :
                                    <img data-aos="imgAni" data-aos-duration="2000" data-aos-easing="liner" src={slcData[props.snum]["isrc"]} alt='' />
                                }
                            </div>
                            {/*      */}
                            {
                                props.snum == 1 &&
                                <>
                                <div className='wrap'>
                                    <img className='pharer' src="./images/dt/main/pharer.png" alt=" " />
                                </div>
                                <div>
                                    <img className='verres' src='./images/dt/main/verres.png' alt=' ' />
                                </div>
                                </>
                            }
                            {
                                props.snum == 2 &&
                                <>
                                <div className='wrap'>
                                    <img data-aos="imgAni" data-aos-duration="2000" data-aos-easing="liner" className='addImg' src="./images/dt/main/3-1.jpg" alt=" " />
                                </div>
                                <div>
                                    <img className='huitres' src='./images/dt/main/huitres.png' alt='  ' />
                                </div>
                                </>
                            }
                        </div>
                    </article>
                </section>
            </div>
        </>
    );
}; /////////////////////////////// SectionIntro  ///////////////////////////////

// 
export default SectionIntro;